.CreateTodoButton {
  background-color: #38b000;
  box-shadow: 0px 5px 25px rgba(0, 127, 95, 0.603);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px !important;
  width: 64px !important;
  z-index: 30;
  transform: rotate(0);
  transition: .3s ease;
}

.CreateTodoButton:hover {
  transform: rotate(224deg);
}